import flatpickr from 'flatpickr';
import 'flatpickr/dist/flatpickr.min.css';

import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

import error from '../img/error.svg';
import check from '../img/check.svg';

let userSelectedDate;
let changeDateValue;
const inputValueTimer = document.querySelector('#datetime-picker');
const startBtn = document.querySelector('button[data-start]');
const dataValueDays = document.querySelector('span[data-days]');
const dataValueHours = document.querySelector('span[data-hours]');
const dataValueMinutes = document.querySelector('span[data-minutes]');
const dataValueSeconds = document.querySelector('span[data-seconds]');

startBtn.setAttribute('disabled', '');
startBtn.classList.add('disabled-btn');
inputValueTimer.classList.add('input-chek');

const options = {
  enableTime: true,
  time_24hr: true,
  defaultDate: new Date(),
  minuteIncrement: 1,
  onClose(selectedDates) {
    if (selectedDates[0] < new Date()) {
      iziToast.show({
        iconUrl: error,
        title: 'Error',
        titleSize: 16,
        message: 'Please choose a date in the future',
        messageColor: '#ffffff',
        titleColor: '#ffffff',
        messageSize: 16,
        backgroundColor: '#EF4040',
        position: 'topRight',
        maxWidth: 902,
        close: false,
      });

      startBtn.classList.add('disabled-btn');
      startBtn.classList.remove('active-btn');
      startBtn.setAttribute('disabled', '');
    } else {
      startBtn.addEventListener('click', successfully);

      function successfully() {
        iziToast.show({
          iconUrl: check,
          title: 'OK',
          titleSize: 16,
          message: 'Timer on',
          messageColor: '#ffffff',
          titleColor: '#ffffff',
          messageSize: 16,
          backgroundColor: '#59A10D',
          position: 'topRight',
          maxWidth: 902,
          close: false,
        });

        return startBtn.removeEventListener('click', successfully);
      }

      startBtn.removeAttribute('disabled');
      startBtn.classList.add('active-btn');
      startBtn.classList.remove('disabled-btn');
      userSelectedDate = selectedDates[0];
    }
  },
};

const addLibryary = flatpickr('#datetime-picker', options);

function convertMs(ms) {
  const second = 1000;
  const minute = second * 60;
  const hour = minute * 60;
  const day = hour * 24;

  const days = Math.floor(ms / day);

  const hours = Math.floor((ms % day) / hour);

  const minutes = Math.floor(((ms % day) % hour) / minute);

  const seconds = Math.floor((((ms % day) % hour) % minute) / second);

  return { days, hours, minutes, seconds };
}

function addLeadingZero(value) {
  if (value < 10) {
    return String(value).padStart(2, '0');
  }
  return value;
}

function updateTimer() {
  const auditDate = userSelectedDate - new Date();
  if (auditDate <= 0) {
    clearInterval(changeDateValue);
    return;
  }

  startBtn.classList.add('disabled-btn');
  startBtn.classList.remove('active-btn');
  startBtn.setAttribute('disabled', '');
  inputValueTimer.setAttribute('disabled', '');
  inputValueTimer.classList.remove('input-check');

  const { days, hours, minutes, seconds } = convertMs(auditDate);

  dataValueDays.textContent = addLeadingZero(days);
  dataValueHours.textContent = addLeadingZero(hours);
  dataValueMinutes.textContent = addLeadingZero(minutes);
  dataValueSeconds.textContent = addLeadingZero(seconds);
}

startBtn.addEventListener('click', startTimer);

function startTimer() {
  updateTimer();
  changeDateValue = setInterval(updateTimer, 1000);
}
